import React, { useState, useEffect, useRef } from "react";
import Cookies from "js-cookie";
import TitlePane from "./titlePane";
import ListPane from "./listPane";
import Board from "./board";
import Controls from "./controls";
import { board } from "./algo";
import { cw, ch, rad, dist } from "./layout";

const Main = () => {
  const thisWidth = cw + 400;
  const thisHeight = ch + 70;
  const jumpCount = 12;
  const jumpInterval = 25;
  const cookie = Cookies.get("chess-theme");
  const [figures, setFigures] = useState();
  const [newGame, setNewGame] = useState(false);
  const [ready, setReady] = useState(0);
  const [theme, setTheme] = useState(cookie ? parseInt(cookie) : 2);
  const [anim, setAnim] = useState(false);
  const [step, setStep] = useState();
  const [game, setGame] = useState();
  const [total, setTotal] = useState(0);
  const [updating, setUpdating] = useState(false);
  const [isPlaying, setIsPlaying] = useState(false);
  const animRef = useRef();
  const intervalRef = useRef();
  const timeoutRef = useRef();

  useEffect(() => {}, [ready]);

  useEffect(() => {
    if (game) {
      setTotal(game.list.length);
      if (step === 0) {
        setNewGame(true);
      } else {
        setStep(0);
      }
    }
  }, [game]);

  useEffect(() => {
    if (game) {
      if (newGame) {
        setNewGame(false);
      }
      const pos = board(game.list[step].fen);
      const mv = game.list[step].move;
      const spec = game.list[step].check ? 2 : game.list[step].kill ? 1 : 0;
      const prv = mv[0] < 0 ? null : [dist(mv[2]) - rad, dist(mv[3]) - rad];
      const lst = [];
      let obj = null;
      pos.forEach((e) => {
        const f = [e[0], dist(e[1]) - rad, dist(e[2]) - rad];
        if (e[1] === mv[0] && e[2] === mv[1]) {
          obj = f;
        } else {
          lst.push(f);
        }
      });
      if (anim && obj) {
        animRef.current = {
          list: lst,
          obj: [obj[0], obj[1], obj[2], prv[0], prv[1]],
          spec: spec,
          count: 0,
        };
        intervalRef.current = setInterval(doAnim, jumpInterval);
      } else {
        if (obj) {
          lst.push(obj);
        }
        setFigures({ items: lst });
      }
    }
  }, [newGame, step]);

  useEffect(() => {
    if (!isPlaying) {
      if (timeoutRef.current) {
        clearTimeout(timeoutRef.current);
      }
    } else if (game && step < game.list.length - 1) {
      go(step + 1, 0);
    }
  }, [isPlaying]);

  const style = {
    padding: "25px 20px 25px 20px",
    backgroundImage: `url("assets/background.png")`,
  };

  const doAnim = () => {
    const v = animRef.current;
    const z = v.obj;
    const done = v.count === jumpCount;
    const x = done ? z[1] : z[3] + ((z[1] - z[3]) * v.count) / jumpCount;
    const y = done ? z[2] : z[4] + ((z[2] - z[4]) * v.count) / jumpCount;
    const a = [...v.list, [z[0], x, y]];
    if (!done) {
      v.count++;
      setFigures({ items: a });
    } else {
      clearInterval(intervalRef.current);
      setAnim(false);
      const z = animRef.current.obj;
      setFigures({ items: a, mark: [z[3], z[4]], hi: [z[1], z[2]], spec: v.spec });
      if (isPlaying) {
        if (step < game.list.length - 1) {
          timeoutRef.current = setTimeout((e) => {
            go(step + 1, 0);
          }, 3000n);
        } else setIsPlaying(false);
      }
    }
  };

  const go = (pos, prv) => {
    if (pos === prv + 1) {
      setAnim(true);
    }
    setStep(pos);
  };

  const onThemeChange = (val) => {
    if (val !== theme) {
      setTheme(val);
      setUpdating(true);
      Cookies.set("chess-theme", val);
    }
  };
  return (
    <div style={style}>
      <div style={{ position: "relative", width: thisWidth, height: thisHeight }}>
        <div style={{ position: "absolute", width: 200, height: ch + 70, top: 0, left: 0 }}>
          <TitlePane width={200} height={ch + 70} game={game} setGame={setGame} />
        </div>
        <div style={{ position: "absolute", width: cw, height: ch + 70, top: 0, left: 200 }}>
          <Board
            figures={figures}
            theme={theme}
            setReady={setReady}
            updating={updating}
            setUpdating={setUpdating}
          />
          <div style={{ width: { cw }, height: "80px", marginLeft: "40px", marginTop: "5px" }}>
            <Controls lmargin={200 + 20} width={cw - 80} height="60" total={total} onPos={go} />
          </div>
        </div>
        <div style={{ position: "absolute", width: 200, height: ch + 70, top: 0, left: cw + 200 }}>
          <ListPane
            width={200}
            height={ch + 70}
            game={game}
            step={step}
            theme={theme}
            onThemeChange={onThemeChange}
          />
        </div>
      </div>
    </div>
  );
};

export default Main;
