import SXQW from "./records/sxqw";
import HYLZ from "./records/hylz";
import WYB from "./records/wyb";
import BGY from "./records/bgy";
import RJDZ from "./records/rjdz";

import MHP from "./records/mhp";
import JZB from "./records/jzb";
import SDMJ from "./records/sdmj";
import JCYP from "./records/jcyp";
import SQYQ from "./records/sqyq";
import MRSJ from "./records/mrsj";
import LKSJ from "./records/lksj";

import CQWB from "./records/cqwb";
import ZGQX from "./records/zgqx";
import CHZB from "./records/chzb";
import SGYY from "./records/sgyy";

const BOOKS = [
  { id: 102, data: SXQW },
  { id: 103, data: HYLZ },
  { id: 104, data: WYB },
  { id: 105, data: BGY },
  { id: 106, data: RJDZ },
  { id: 201, data: JZB },
  { id: 202, data: MHP },
  { id: 203, data: SQYQ },
  { id: 204, data: LKSJ },
  { id: 205, data: JCYP },
  { id: 206, data: MRSJ },
  { id: 207, data: SDMJ },
  { id: 301, data: CQWB },
  { id: 302, data: ZGQX },
  { id: 303, data: CHZB },
  { id: 304, data: SGYY },
];

const books = () => {
  return BOOKS.map((a) => ({ id: a.id, name: a.data.name, type: a.data.type }));
};

const book4 = (match) => {
  return BOOKS.find(function (e) {
    return e.id === match || e.data.name === match;
  });
};

const game4Book = (book) => {
  const res = book4(book);
  if (!res) {
    console.error("No game found for " + book);
    return undefined;
  }
  return res.data.games;
};

const game4 = (book, index) => {
  const res = book4(book);
  if (!res) {
    return undefined;
  }

  const steps = res.data.games;
  return index < 0 || index > steps.length ? undefined : steps[index];
};

const sublists = (list, num) => {
  const res = [];
  for (let i = 0; i < list.length; i += num) {
    res.push(list.slice(i, Math.min(list.length, i + num)));
  }
  return res;
};

export { BOOKS, books, book4, game4, game4Book, sublists };
