import React, { useState, useEffect, useRef } from "react";
import "./controls.css";

const Controls = ({ lmargin, startLast, total, width, height, onPos }) => {
  const [play, setPlay] = useState();
  const timeRef = useRef();
  const speedRef = useRef();
  const valueRef = useRef();
  const divRef = useRef();
  const posRef = useRef(0);
  const delayRef = useRef(1000);
  const time0 = Math.log(60000);
  const timeMax = Math.log(100);
  const time4 = (f) => {
    return f <= 0 ? 60000 : f >= 1 ? 100 : Math.round(Math.exp(time0 + f * (timeMax - time0)));
  };
  const scale4 = (delay) => {
    return delay >= 60000 ? 1 : delay <= 100 ? 1 : (Math.log(delay) - time0) / (timeMax - time0);
  };
  const [scale, setScale] = useState(scale4(delayRef.current));
  const barLen = 120;
  const barHeight = 22;
  const playRef = useRef();

  useEffect(() => {
    if (total !== undefined) {
      const pos0 = startLast ? total - 1 : 0;
      posRef.current = pos0;
      let go = false;
      playRef.current = go;
      setPlay(go);
      if (!go) {
        onPos(pos0, -2);
      }
    }
  }, [total]);

  useEffect(() => {
    if (play) {
      startPlay();
    } else {
      stopPlay();
    }
  }, [play]);

  const btnSize = 32;
  const yoff = 3;
  const xoff = 80;

  const Icon = ({ type, size, disabled }) => {
    const path =
      type === 0
        ? "M10,12 L17,17 L17,7Z M5,12 L12,17 L12,7Z"
        : type === 1
        ? "M5,12 L12,17 L12,7Z M16,17 L16,7 L14,7 L14,17Z"
        : type === 2
        ? "M19,12 L12,17 L12,7Z M8,17 L8,7 L10,7 L10,17Z"
        : type === 3
        ? "M14,12 L7,17 L7,7Z M19,12 L12,17 L12,7Z"
        : type === 4
        ? "M8,5 L19,12 L8,19Z"
        : "M7,18 L7,6 L10,6 L10,18 L7,18 Z M14,18 L14,6 L17,6 L17,18 L14,18 Z";

    return (
      <svg viewBox="0 0 24 24" width={size} height={size}>
        <linearGradient id="playOuter" x1="0%" y1="100%" x2="100%" y2="0%">
          <stop stopColor="#4ba" offset="0%" />
          <stop stopColor="#278" offset="100%" />
        </linearGradient>
        <linearGradient id="playCircle" x1="100%" y1="0%" x2="0%" y2="100%">
          <stop stopColor="#9ef" offset="0%" />
          <stop stopColor="#5ba" offset="100%" />
        </linearGradient>
        <linearGradient id="playFigure" x1="0%" y1="100%" x2="100%" y2="0%">
          <stop stopColor="#167" offset="0%" />
          <stop stopColor="#023" offset="100%" />
        </linearGradient>

        <circle
          cx="12"
          cy="12"
          r="11.2"
          fill={disabled ? "#59a" : "url('#playCircle')"}
          stroke={disabled ? "#333" : "url('#playOuter')"}
          strokeWidth="1.4"
          opacity={disabled ? 0.5 : 1}
        />
        <path
          d={path}
          fill={disabled ? "#035" : "url('#playFigure')"}
          opacity={disabled ? 0.5 : 1}
        />
      </svg>
    );
  };

  const PosBar = ({ pos, total, width, height }) => {
    const z = !pos || pos === 0 ? 0 : Math.round(((pos + 1) * 120) / total);
    return (
      <svg viewBox="0 0 120 22" width={width} height={height}>
        <path d={`M0 4L${z} 4L${z} 20L0 20Z`} fill={"#2a9"} stroke="none" />
        <path d={`M${z} 4L120 4L120 20L${z} 20Z`} fill={"#045"} stroke="none" />
        {z > 2 && z < 90 && (
          <text x={z} y={16} fill="#ff0" stroke="none" textAnchor="middle" fontSize="12">
            {pos}
          </text>
        )}
        <text x={118} y={16} fill="#eee" stroke="none" textAnchor="end" fontSize="12">
          {total}
        </text>
      </svg>
    );
  };

  const SpeedBar = ({ scale, width, height }) => {
    const z = !scale ? 1 : 1 + Math.round(scale * 118);
    return (
      <svg viewBox="0 0 120 22" width={width} height={height}>
        <text x={116} y={12} fill="#7de" stroke="none" textAnchor="end" fontSize="12">
          快
        </text>
        <text x={4} y={12} fill="#7de" stroke="none" textAnchor="start" fontSize="12">
          慢
        </text>
        <path d="M1 18 L119 18" fill="none" stroke="#045" strokeWidth={2} />
        <path d="M1 14 L1 22" fill="none" stroke="#045" strokeWidth={2} />
        <path d="M119 14 L119 22" fill="none" stroke="#045" strokeWidth={2} />
        <path
          d={`M${z - 6} 10 L${z} 5L${z + 6} 10L${z + 6} 22L${z - 6} 22Z`}
          fill="#298"
          stroke="none"
        />
      </svg>
    );
  };

  const go = () => {
    if (!playRef.current) {
      if (timeRef.current) {
        clearTimeout(timeRef.current);
        timeRef.current = null;
      }
    } else if (posRef.current === total - 1) {
      playRef.current = false;
      setPlay(false);
      timeRef.current = null;
    } else {
      posRef.current++;
      startPlay();
    }
  };

  const startPlay = () => {
    let a = posRef.current;
    walk(a, a - 1);
    if (a <= total - 1) {
      timeRef.current = setTimeout(() => {
        go();
      }, delayRef.current);
    }
  };

  const stopPlay = () => {
    if (timeRef.current) {
      clearTimeout(timeRef.current);
      timeRef.current = null;
    }
  };

  const makeIcon = (type, size, disabled, x, y, onClick) => {
    return (
      <button
        className="iconButton"
        onClick={onClick}
        style={{ position: "absolute", top: y, left: x, width: size, height: size }}
      >
        <Icon type={type} size={size} disabled={disabled} />
      </button>
    );
  };

  const walk = (current, prev) => {
    posRef.current = current;
    onPos(current, prev);
  };

  const onFirst = () => {
    walk(0, posRef.current);
  };

  const onLast = () => {
    walk(total - 1, posRef.current);
  };

  const onPrev = () => {
    if (posRef.current > 0) {
      walk(posRef.current - 1, posRef.current);
    }
  };

  const onNext = () => {
    if (posRef.current < total - 1) {
      walk(posRef.current + 1, posRef.current);
    }
  };

  const onPlayPause = () => {
    playRef.current = !playRef.current;
    setPlay(playRef.current);
  };

  const onSpeed = (e) => {
    const x = e.clientX - divRef.current.offsetLeft - speedRef.current.offsetLeft - lmargin;
    if (x > 2 && x < barLen - 2) {
      const nrate = time4(x / barLen);
      if (Math.abs(nrate - delayRef.current) / Math.min(delayRef.current, nrate) > 0.1) {
        delayRef.current = nrate;
        setScale(x / barLen);
      }
    }
  };

  const onValue = (e) => {
    if (!play) {
      const x = e.clientX - divRef.current.offsetLeft - valueRef.current.offsetLeft - lmargin;
      if (x > 2 && x < barLen - 2) {
        const npos = Math.round((x * total) / barLen);
        if (npos > 0 && npos < total - 1 && Math.abs(npos - posRef.current) > 2) {
          walk(npos, posRef.current);
        }
      }
    }
  };

  return (
    <div ref={divRef}>
      <div style={{ position: "relative", width: width, height: height }}>
        {makeIcon(0, btnSize, play || posRef.current === 0, 2, yoff, onFirst)}
        {makeIcon(1, btnSize, play || posRef.current === 0, xoff, yoff, onPrev)}
        {makeIcon(
          2,
          btnSize,
          play || posRef.current >= total - 1,
          width - xoff - btnSize,
          yoff,
          onNext
        )}
        {makeIcon(
          3,
          btnSize,
          play || posRef.current === total - 1,
          width - 2 - btnSize,
          yoff,
          onLast
        )}
        {makeIcon(play ? 5 : 4, 48, false, width / 2 - 24, 10, onPlayPause)}
        <span
          ref={speedRef}
          className="svgSpan"
          onClick={(e) => {
            onSpeed(e);
          }}
          style={{
            position: "absolute",
            top: btnSize + 13,
            left: 0,
            width: barLen,
            height: barHeight,
          }}
        >
          <SpeedBar scale={scale} width={barLen} height={barHeight} />
        </span>
        <span
          ref={valueRef}
          className="svgSpan"
          onClick={(e) => {
            onValue(e);
          }}
          style={{
            position: "absolute",
            top: btnSize + 13,
            left: width - barLen,
            width: barLen,
            height: barHeight,
          }}
        >
          <PosBar pos={posRef.current} total={total} width={barLen} height={barHeight} />
        </span>
      </div>
    </div>
  );
};
export default Controls;
