import React, { useState } from "react";
import ThemeChooser from "./themeChooser";
import "./controls.css";

const ListPane = ({ width, height, game, step, theme, onThemeChange }) => {
  const [themeDialog, setThemeDialog] = useState(0);
  const MoveList = ({ list, pos }) => {
    const thisWidth = width - 10;
    const ch = 27;
    const max = 16;
    const ln = Math.trunc((pos - 1) / 2);
    const ls = ln < max ? 0 : ln - max;
    const thisHeight = (ln + 1 - ls) * ch + 14;

    const aLine = (num, y, a, b) => {
      const res = [];
      res.push(
        <rect
          key={num + "NumRect"}
          fill={num % 2 === 0 ? "#eff" : "#efffef"}
          x={0}
          y={y}
          width={thisWidth}
          height={ch}
        />
      );
      res.push(
        <text key={num + "NumTxt"} fill="#227" x="28" y={y + 20} fontSize="14" textAnchor="end">
          {num}
        </text>
      );
      res.push(
        <text
          key={num + "TxtA"}
          fill="#722"
          x="44"
          y={y + 20}
          fontSize="14"
          fontFamily="sans-serif"
        >
          {a}
        </text>
      );
      if (b) {
        res.push(
          <text
            key={num + "TxtB"}
            fill="#333"
            x="120"
            y={y + 20}
            fontSize="14"
            fontFamily="monospace"
          >
            {b}
          </text>
        );
      }
      return res;
    };

    const lines = () => {
      let res = [];
      let y = 1;
      for (let i = ls, j = 2 * i + 1; i <= ln; j += 2, i++) {
        res = res.concat(aLine(i + 1, y, list[j].note, j + 1 <= pos ? list[j + 1].note : null));
        y += ch;
      }
      return res;
    };

    return (
      <svg viewBox={`0 0 ${thisWidth} ${thisHeight}`} width={thisWidth} height={thisHeight}>
        {lines()}
      </svg>
    );
  };

  const Name = ({ fill, stroke, name, width, height }) => {
    return (
      <svg viewBox="0 0 160 36" width={width} height={height}>
        <text
          fill={fill}
          stroke={stroke}
          fontSize={name.length <= 3 ? 32 : name.length === 4 ? 28 : name.length === 5 ? 26 : 24}
          fontFamily="serif"
          fontWeight="bold"
          x="5"
          y="30"
          textAnchor="start"
          strokeWidth={2}
        >
          {name.length > 6 ? name.substring(0, 6) : name}
        </text>
      </svg>
    );
  };

  const ThemeButton = ({ size }) => {
    const clr = "#555";
    const wd = 3;
    const op = 0.9;
    return (
      <svg viewBox="0 0 24 24" width={size} height={size}>
        <line x1="0" y1="2" x2="24" y2="2" stroke={clr} strokeWidth={wd} opacity={op} />
        <line x1="0" y1="12" x2="24" y2="12" stroke={clr} strokeWidth={wd} opacity={op} />
        <line x1="0" y1="22" x2="24" y2="22" stroke={clr} strokeWidth={wd} opacity={op} />
      </svg>
    );
  };

  return (
    <>
      <div style={{ position: "relative", width: { width }, height: { height } }}>
        <button
          className="iconButton"
          onClick={() => {
            setThemeDialog(1);
          }}
          style={{
            background: "transparent",
            padding: 0,
            border: "none",
            position: "absolute",
            left: 200 - 28,
            top: height - 18,
          }}
        >
          <ThemeButton size="18" />
        </button>
        {game && game.red && (
          <div
            style={{
              position: "absolute",
              top: height - 110,
              left: 10,
            }}
          >
            <Name
              name={!game || !game.red ? "" : game.red}
              width={160}
              height={36}
              fill="#fff"
              stroke="#f00"
            />
          </div>
        )}
        {game && game.list && step && (
          <div
            style={{
              position: "absolute",
              top: 0,
              left: 10,
              color: "white",
            }}
          >
            {!game || !game.list || !step ? "" : <MoveList list={game.list} pos={step} />}
          </div>
        )}
        {game && game.res && (
          <span
            style={{
              position: "absolute",
              top: height - 50,
              left: 20,
              color: "#7ae",
              fontFamily: "sans-serif",
              fontWeight: 500,
              fontSize: 16,
            }}
          >
            {!game || !game.res ? "" : game.res}
          </span>
        )}
        {game && game.list && (
          <span
            style={{
              position: "absolute",
              top: height - 20,
              left: 20,
              color: "#8bf",
              fontFamily: "sans-serif",
              fontWeight: 400,
              fontSize: 14,
            }}
          >
            {!game || !game.list ? "" : `共${Math.trunc((game.list.length + 1) / 2)}回合`}
          </span>
        )}
      </div>
      <ThemeChooser
        theme={theme}
        themeDialog={themeDialog}
        onThemeChange={onThemeChange}
        setThemeDialog={setThemeDialog}
      />
    </>
  );
};
export default ListPane;
