import React, { useState, useEffect } from "react";
import Cookies from "js-cookie";
import BookChooser from "./bookChooser";
import GameChooser from "./gameChooser";
import "./controls.css";

import { buildList } from "./algo";
import { game4Book } from "./data";

const TitlePane = ({ width, height, game, setGame }) => {
  const cookie = Cookies.get("chess-game");
  const [bookDialog, setBookDialog] = useState(0);
  const [gameDialog, setGameDialog] = useState(0);

  const [select, setSelect] = useState(
    cookie ? JSON.parse(cookie) : { book: "适情雅趣", index: 0 }
  );

  useEffect(() => {
    if (select) {
      setGame(buildGame(select.book, select.index));
      Cookies.set("chess-game", JSON.stringify(select));
    }
  }, [select]);

  const buildGame = (book, index) => {
    const data = game4Book(book);
    const now = data[index];
    const list = buildList(now[2], now[3]);
    return {
      index: index,
      book: book,
      name: now[0],
      list: list,
      res: now[1] === "未知" || now[1].length === 0 ? null : now[1],
      red: now.length > 4 ? now[4] : null,
      black: now.length > 5 ? now[5] : null,
    };
  };

  const toRows = (txt) => {
    const lines = [];
    for (let i = 0, n = txt.length; i < n; ) {
      const c = txt.charCodeAt(i);
      if (c > 256) {
        lines.push(txt.substring(i, i + 1));
        i++;
      } else {
        let k = n;
        for (let j = i + 1; j < n; j++) {
          const c = txt.charCodeAt(j);
          if (c > 256) {
            k = j;
            break;
          }
        }
        lines.push(txt.substring(i, k));
        i = k;
      }
    }
    return lines;
  };

  const Arrow = ({ size, type }) => {
    return (
      <svg viewBox="0 0 24 24" width={size} height={size}>
        <path
          fill={type === 0 ? "#880" : "#073"}
          stroke="none"
          d={type === 0 ? "M2,2 L22,2 L12,20Z" : "M2,22 L22,22 L12,4Z"}
          opacity={0.9}
        />
      </svg>
    );
  };

  const Title = ({ fill, stroke, title, width, height }) => {
    const line = (num, ch, fz, y, lw) => {
      return (
        <text
          key={num + "ltitle"}
          fill={fill}
          stroke={stroke}
          fontSize={fz}
          fontFamily="sans-serif"
          fontWeight="bold"
          x={35}
          y={y}
          textAnchor="middle"
          strokeWidth={lw}
        >
          {ch}
        </text>
      );
    };

    const lines = () => {
      const rows = toRows(title);
      const n = rows.length;
      const sz = n <= 10 ? 40 : rows.length <= 16 ? 36 : 32;
      const lh = sz * 1.3;
      const lw = n <= 10 ? 1.45 : 0.75;
      const res = [];
      let y = sz - 4;
      for (let i = 0; i < n; i++) {
        res.push(line(i, rows[i], sz, y, lw));
        y += lh;
      }
      return res;
    };

    return (
      <svg viewBox="0 0 70 520" width={width} height={height}>
        {lines()}
      </svg>
    );
  };

  const Subtitle = ({ fill, subtitle, width, height }) => {
    const line = (num, ch, fz, y) => {
      return (
        <text
          key={num + "subtitle"}
          fill={fill}
          stroke="none"
          fontSize={fz}
          fontFamily="serif"
          x={35}
          y={y}
          textAnchor="middle"
        >
          {ch}
        </text>
      );
    };

    const lines = () => {
      const i = subtitle.indexOf("-");
      const rows = toRows(i < 0 ? subtitle : subtitle.substring(0, i));
      const n = rows.length;
      const sz = n <= 10 ? 28 : rows.length <= 16 ? 26 : 22;
      const lh = sz * 1.3;
      const lw = n <= 10 ? 2 : 1.5;
      const res = [];
      let y = height - 30;
      for (let i = n - 1; i >= 0; i--) {
        res.push(line(i, rows[i], sz, y, lw));
        y -= lh;
      }
      return res;
    };

    return (
      <svg viewBox="0 0 70 520" width={width} height={height}>
        {lines()}
      </svg>
    );
  };

  const Name = ({ fill, stroke, name, width, height }) => {
    return (
      <svg viewBox="0 0 140 36" width={width} height={height}>
        <text
          fill={fill}
          stroke={stroke}
          fontSize={name.length <= 3 ? 32 : name.length === 4 ? 26 : name.length === 5 ? 24 : 22}
          fontFamily="serif"
          fontWeight="bold"
          x={name.width >= 6 ? 140 : 126}
          y="28"
          textAnchor="end"
          strokeWidth={name.length <= 3 ? 1 : 0.75}
        >
          {name.length > 6 ? name.substring(0, 6) : name}
        </text>
      </svg>
    );
  };

  return (
    <>
      <div style={{ position: "relative", width: { width }, height: { height } }}>
        {game && game.black && (
          <div
            style={{
              position: "absolute",
              top: 0,
              left: 60,
            }}
          >
            <Name name={game.black} width={140} height={36} fill="#000" stroke="#fff" />
          </div>
        )}
        <button
          className="iconButton"
          onClick={() => {
            setBookDialog(1);
          }}
          style={{
            position: "absolute",
            top: 0,
            left: 24,
          }}
        >
          <Arrow size={22} type={0} />
        </button>

        {game && game.book && (
          <div
            style={{
              position: "absolute",
              top: 30,
              left: 0,
            }}
          >
            <Title title={game.book} width={70} height={520} fill="#f00" stroke="#ff5" />
          </div>
        )}
        {game && game.name && (
          <div
            style={{
              position: "absolute",
              top: 100,
              left: 120,
            }}
          >
            <Subtitle subtitle={game.name} width={70} height={520} fill="#8d6" />
          </div>
        )}
        <button
          className="iconButton"
          onClick={() => {
            setGameDialog(1);
          }}
          style={{
            position: "absolute",
            top: 572,
            left: 104,
          }}
        >
          <Arrow size={22} type={1} />
        </button>
      </div>
      <BookChooser
        book={select.book}
        bookDialog={bookDialog}
        setSelect={setSelect}
        setBookDialog={setBookDialog}
      />
      <GameChooser
        book={select.book}
        index={select.index}
        gameDialog={gameDialog}
        setSelect={setSelect}
        setGameDialog={setGameDialog}
      />
    </>
  );
};
export default TitlePane;
